@import "../styles/variables";

.wrapper {
  width: 712px;
  height: 755px;
  border-radius: 50px;
  background-color: $primary-color;
  margin: 96px auto;

  .header {
    margin: 0 auto;
    width: 435px;
    display: flex;
    justify-content: space-between;
    padding-top: 32px;

    h6 {
      font-weight: 500;
      font-size: 36px;
      color: #FFFFFF;
      cursor: pointer;

      &.active {
        border-bottom: 1px #FFFFFF solid;
      }
    }
  }

  .container {
    margin: 0 auto;
    width: 580px;

    .banner {
      display: flex;
      justify-content: center;
      margin: 50px 0;

      .text {
        margin-left: 30px;
        padding-top: 9px;
        user-select: none;

        h1 {
          font-weight: 700;
          font-size: 32px;
        }
        h2 {
          font-weight: 400;
          font-size: 26px;
          line-height: 39px;
          color: #FFFFFF;
        }
      }
    }
  input {
    width: 554px;
    height: 70px;
    border: none;
    border-radius: 20px;
    padding-left: 26px;
    font-weight: 400;
    font-size: 26px;
    color: rgba(0, 0, 0, 0.69);
    margin-bottom: 25px;

    &:focus {
      outline: none;
    }
  }

  button {
    display: block;
    margin: 25px auto 0;
    width: 250px;
    height: 57px;
    background: #000000;
    border-radius: 20px;
    font-weight: 400;
    font-size: 24px;
    color: #FFFFFF;
    cursor: pointer;
  }

  .google {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 85%;
    height: 65px;
    margin-top: 55px;

    img {
      width: 35px;
      margin-right: 30px;
      }
    }
  }
}